* {
    margin:0;
    padding: 0;
}

*,
*::after,
*::before{
    box-sizing: inherit;
}

html{
    box-sizing: border-box;
    font-size: 62.5% ;
}

body{
    padding: 3rem 2rem;
    font-family: 'Poppins', sans-serif;
    font-size: 1.5rem;
    font-weight: 400;
    /* background-image: linear-gradient(to right, lightblue, rgb(144, 144, 230) ); */
}

.container{
    border: 1px solid blue;
    border-radius: 5px;
    margin: 0 auto;
    max-width: 100rem;
    padding: 3rem;
}

.header{
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
}

.header__title{
    font-size: 3rem;
}

.btn{
    background-color: lightgreen;
    border-color: lightgray;
    border-radius: 5px;
    box-shadow: 0 .7rem 1rem rgba(0, 0, 0, .2);
    color: #fff;
    cursor: pointer;
    font-size: 1.3rem;
    padding: 1rem 2rem;    
}

.btn:active{
    transform: translateY(2px);   
}

.btn:focus{
    outline: none;    
}

.form__item{
    margin-bottom: 2rem;  
}

.label{
    display: block;
    font-weight: bold;
}

.input{
    border: 1px solid lightgray;
    border-radius: 5px;
    width: 100%;
    padding: 1.5rem;
}

.form__item--check{
    display: flex; 
    align-items: center;   
}

.checkbox{
    flex-basis: 20%;
    font-weight: bold;
}

.btn--submit{
    background-color: #000;
    width: 100%;
}

.tasks{
    margin: 2.5rem 0;
}

.task-item{
    background-color: rgb(238, 236, 236);
    padding: 1rem 1.5rem;
    margin-bottom: 1rem;    
}

.task-item p{
    font-size: 1.3rem;
}

.border-left{
    border-left: 7px solid lightgreen;
}

.footer{
    text-align: center;
}

h4{
    display:flex;
    justify-content: space-between;
    align-items: center;
}

.link{
    text-decoration: none;
}

.link:active, .link:visited{
    color: #000;
}